/** Motif repeats on Overview page */

import {
  Chart,
  Motif,
  MotifColor,
  getChartFromMotifs,
  getMergedChart,
  getMotifFilledChart,
  getRightLeftFlippedChart,
  getRound,
  getStsCountFromChartRow,
} from "@tanyoknits/shared";
import { Size, VestMeasurement } from "../../utils/sizeBase";
import { measurements, sizes } from "./sizes";
import { MotifRepeat } from "../../utils/types";
import { braid, zigzag, diamond, diamondNoBobble, moss } from "./stitches";
import { getMotifBySize } from "../../utils/misc";
import profile from "./profile";

// Gutters
const K1P2 = "k p:2";
const K2P2 = "k:2 p:2";
const K3P3 = "k:3 p:3";
const P1 = "p";
const P2 = "p:2";
const P3 = "p:3";
const gutters: Partial<Record<Size, string[]>> = {
  "2XS": [K2P2, P1, P1],
  XS: [K1P2, P1, P1],
  S: [K2P2, P2, P1],
  M: [K2P2, P2, P2],
  L: [K2P2, P2, P2],
  XL: [K3P3, P3, P3],
  "2XL": [K3P3, P3, P3],
  "3XL": [K3P3, P3, P3],
  "4XL": [K3P3, P3, P3],
};
const motifColors: MotifColor[] = ["orange", "blue", "pink"];

export interface MotifRepeats {
  frontChart: Chart;
  backChart: Chart;
  motifs: MotifRepeat[];
}

export function getMotifRepeatsBySize(sizeID: number): MotifRepeats {
  const size = sizes[sizeID];
  const motifs = [braid, zigzag, diamond].map((m, i) => {
    return { ...getMotifBySize(m, size), id: motifColors[i] };
  });
  const motifCAlt: Motif = {
    ...getMotifBySize(diamondNoBobble, size),
    id: "pink",
  };

  const gutter: string[] = gutters[size] as string[];
  const chartOnRight = getChartFromMotifs([
    gutter[0],
    motifs[0],
    gutter[1],
    motifs[1],
    gutter[2],
  ]);
  const chartAtCenterFront = getChartFromMotifs([motifs[2]]);
  const chartAtCenterBack = getChartFromMotifs([motifCAlt]);

  const chartOnLeft = getRightLeftFlippedChart(chartOnRight);
  const motifChartFront = getMergedChart([
    chartOnRight,
    chartAtCenterFront,
    chartOnLeft,
  ]);
  const motifChartBack = getMergedChart([
    chartOnRight,
    chartAtCenterBack,
    chartOnLeft,
  ]);

  // side motif sts count on one side of body
  const totalStEquivCount = getStsCountFromChartRow(motifChartFront[0], true);
  const ms = measurements[sizeID] as VestMeasurement;
  const width = ms.bust_circumference / 2;
  const stsGauge = profile.gauge.sts_count / 4;
  const motifSide: Motif = getMotifBySize(moss, size);
  const sideMotifStsCount =
    (getRound(
      ((width - totalStEquivCount / stsGauge) * stsGauge) /
        (motifSide.stEquivCount ?? motifSide.stsCount)
    ) -
      // moss stitch is repeat of 4, we need symmetrical, thus subtract 0.5 repeats
      0.5) *
    motifSide.stsCount;

  const chartOnSideLeft = getMotifFilledChart(
    motifSide.chart,
    sideMotifStsCount / 2,
    motifChartFront.length
  );
  const chartOnSideRight = getRightLeftFlippedChart(chartOnSideLeft);

  return {
    frontChart: getMergedChart([
      chartOnSideRight,
      motifChartFront,
      chartOnSideLeft,
    ]),
    backChart: getMergedChart([
      chartOnSideRight,
      motifChartBack,
      chartOnSideLeft,
    ]),
    motifs: motifs.map((m) => {
      return {
        id: m.id,
        title: m.title,
        times: motifChartFront.length / m.chart.length,
      };
    }),
  };
}
